@media screen and (min-width: 1px) {
    .project-1 {
        background-image: url("../../assets/project-1.jpg");
    }

    .project-2 { 
        background-image: url("../../assets//project-2.jpg");
    }

    .app-work-section { 
        display: grid;
        grid-template-rows: auto 1fr;
        row-gap: 1.5em;
        padding-top: 7em;
        margin-bottom: 10em;
    }

    .app-work-section__title { 
        font-size: var(--small-title-size);
        color: var(--color-gray);
    }

    .app-work-section__projetcts { 
        display: grid;
        grid-template-rows: 1fr;
        row-gap: 4em;
    }

    .app-work-section__projetcts__1 {
        position: relative;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        width: 100%;
        min-height: 330px;

    }

    .app-work-section__projects__1__details {
        padding: 1em;
        background: rgba(31, 24, 63, 0.8);
        display: flex;
        flex-direction: column;
        justify-content: center;
        row-gap: 1em;
        min-height: 330px;
    }

    .app-work-section__projects__1__details__title {
        font-size: var(--small-project-title-size);
        font-weight: normal;
        color: var(--color-gray);
        align-self: center;
    }

    .app-work-section__projects__1__details__description {
        background-color: var(--color-violet-dark);
        padding: 1em;
        box-shadow: 0px 0px 22px rgba(0, 0, 0, 0.3);
    }

    .app-work-section__projects__1__details__description__p {
        color: var(--color-gray);
        font-size: var(--small-text-size);
        line-height: var(--text-line-height);
        text-align: center;
    }

    .app-work-section__projects__1__details__technologies {
        display: flex;
        flex-direction: row;
        column-gap: 1em;
        align-self: center;
    }

    .app-work-section__projects__1__details__technologies__item {
        color: var(--color-gray);
        list-style-type: none;
        font-size: 0.9em;
    }
    
    .app-work-section__projects__1__details__button {
        background: transparent;
        border: none;
        width: 20px;
        height: 20px;
        align-self: center;
        border-radius: 9999px;
    }

    .app-work-section__projects__1__details__button:hover{
        opacity: 0.5;
    }
}


@media screen and (min-width: 576px) { 
    .app-work-section__title { 
        font-size: var(--large-title-size);
    }

    .app-work-section__projects__1__details__button {
        width: 30px;
        height: 30px;
    }
}

@media screen and (min-width: 700px) {
    .app-work-section__projetcts__1 {
        justify-self: center;
    }

}

@media screen and (min-width: 900px) {
    .app-work-section__projetcts__1 {
        width: 65%;
        justify-self: start;
    }

    .app-work-section__projects__1__overlay { 
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 10;
        background-color: rgba(31, 24, 63, 0.8);
        transition: background 300ms ease;
    }

    .app-work-section__projects__1__overlay:hover {
        background: transparent;
        transition: background 300ms ease;
    }

    .app-work-section__projects__1__details {
        padding: 0;
        position: absolute;
        background: transparent;
        z-index: 10;
        top: 15%;
        left: 75%;
        width: 510px;
        justify-content: start;
    }

    .app-work-section__projects__1__details__title {
        align-self: flex-end;
    }

    .app-work-section__projects__1__details__description__p {
        text-align: right;
    }

    .app-work-section__projects__1__details__technologies {
        align-self: flex-end;
    }

    .app-work-section__projects__1__details__button{
        align-self: flex-end;
    }

    .project-reversed { 
        justify-self: end;
    }

    .project__details--reversed { 
        left: -54%;
    }
    
    .project__details__title--reversed { 
        align-self: flex-start;
    }

    .project__details__description__p--reversead { 
        text-align: left;
    }

    .project__details__technologies--reversed { 
        align-self: flex-start;
    }

    .project__details__button--reversed {
        align-self: flex-start;
    }
}