@media screen and (min-width: 1px) {
    .app-contact-section {
        display: flex;
        flex-direction: column;
        justify-content: start;
        align-items: center;
        row-gap: 1em;
        padding-top: 7em;
        margin-bottom: 18em;
    }

    .app-contact-section__title {
        color: var(--color-primary);
        font-size: var(--small-title-size);
        text-transform: capitalize;
    }

    .app-contact-section__text { 
        color: var(--color-gray);
        font-size: var(--small-text-size);
        line-height: var(--text-line-height);
        text-align: center;
        margin-bottom: 2em;
    }

    .app-contact-section___button {
        align-self: start;
        border: none;
        outline: none;
        background-color: var(--color-primary);
        padding: 0.5em 1em;
        color: var(--color-white);
        font-size: 1em;
        align-self: center;
    }

    .app-contact-section___button__content { 
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        column-gap: 0.3em;
    }

    .app-contact-section___button__content:hover { 
        text-decoration: none;
    }

    .app-contact-section___button__content p {
        text-decoration: none;
        color: white;
    }

    .app-contact-section___button img { 
        width: 15px;
        height: 15px;
    }

    .app-contact-section___button:hover { 
        background-color: var(--color-violet);
    }
}

@media screen and (min-width: 576px) {
    .app-contact-section__title { 
        font-size: var(--large-title-size);
    }       

    .app-contact-section__text { 
        font-size: var(--large-text-size);
        max-width: 500px;
    }

    .app-contact-section___button {
        padding: 1.1em 2em;
        font-size: 1em;
    }

    .app-contact-section___button img {
        width: 20px;
        height: 20px;
    }    

}