@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;900&display=swap');

* {
    padding: 0;
    margin: 0;
}

:root { 
    --color-primary: #473BF0;
    --color-violet: #514AB7;
    --color-violet-dark: #161939;
    --color-gray: #ACADBC;
    --color-mobile-menu: #0c0113;
    --color-black: rgb(0, 5, 0);
    --color-white: #FFFFFF;
    --main-navgiation-menu-item-font: 1.3em;
    --small-title-size: 2.5em;
    --small-project-title-size: 1.5em;
    --small-text-size: 1em;
    --large-title-size: 3em;
    --large-project-title-size: 2em;
    --large-text-size: 1.1em;
    --text-line-height: 1.3em;
}

body { 
    font-size: 1em;
    font-family: "Roboto";
    background-color: var(--color-black);
    overflow-x: hidden;
    max-width: 100%;
}

html { 
    scroll-behavior: smooth;
}

#root {
    display: grid;
    grid-template-rows: 1fr;
    justify-content: center;
    position: relative;
}

.item-blured { 
    filter: blur(8px);
    transition: filter 300ms ease;
}

.item-not-blured { 
    filter: blur(0);
    transition: filter 300ms ease;
}

.highlight { 
    color: var(--color-primary);
    font-weight: bold;
}

@media screen and (min-width: 1px) {
    #root { 
        margin: 0 3em;
    }
}

@media screen and (min-width: 1024px) { 
    #root { 
        margin: 0;
    }
}
