@media screen and (min-width: 1px) {
    .app-hero-section { 
        display: flex;
        flex-direction: column;
        justify-content: center;
        justify-self: center;
        row-gap: 1.5em;
        height: 100vh;
    }
    
    .app-hero-section__heading { 
        display: grid;
        grid-template-rows: repeat(2, auto);
    }
    
    .app-hero-section__heading__title {
        color: var(--color-primary);
        font-size: 3em;
        font-weight: bolder;
    }
    
    .app-hero-section__heading__sub-title { 
        color: var(--color-gray);
        font-size: var(--small-title-size);
        font-weight: bold;
    }
    
    .app-hero-section__description {
        color: var(--color-gray);
        font-size: var(--small-text-size);
        line-height: var(--text-line-height);
        max-width: 400px;
    }
    
    .app-hero-section__button { 
        align-self: start;
        border: none;
        outline: none;
        background-color: var(--color-primary);
        padding: 0.5em 1em;
        color: var(--color-white);
        font-size: 1em;
    }

    .app-hero-section__button:hover { 
        background-color: var(--color-violet);
    }
}

@media screen and (min-width: 576px) {
    .app-hero-section__description { 
        font-size: var(--large-text-size);
        max-width: 500px;
    }
} 

@media screen and (min-width: 1024px) {
    .app-hero-section { 
        row-gap: 2em;
    }

    .app-hero-section__heading {
        row-gap: 0.3em;
    }

    .app-hero-section__heading__title {
        font-size: 3.5em;
    }

    .app-hero-section__heading__sub-title { 
        font-size: var(--large-title-size);
    }

    .app-hero-section__button {
        padding: 1.1em 2em;
        font-size: 1em;
    }
} 
