.header-hidden { 
    transform: translatey(-100%);
    transition: transform 300ms ease;
}

.header-visible { 
    transform: translateY(0);
    transition: transform 300ms ease;
}

@media screen and (min-width: 1px) {
    .app-header { 
        position: fixed;
        top: 0;
        left: 0;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        padding: 1em 1em;
        background-color: rgba(0, 5, 0, 0.5);
        backdrop-filter: blur(8px);
        z-index: 20;
        width: 100%;
    }

    .app-header__logo img { 
        width: 43px;
        height: 43px;
    }
    
    .app-header__main-navigation { 
        display: none;
    }

    .app-header__mobile-navigation { 
    }

    .app-header__mobile-navigation__button { 
        background: transparent;
        border: none;
        outline: none;
        display: flex;
        flex-direction: column;
        row-gap: 0.3em;
    }

    .app-header__mobile-navigation__button__line {
        width: 1.5em;
        height: 0.2em;
        background-color: var(--color-white);
    }

    .app-header__mobile-navigation__menu { 
        position: fixed;
        top: 0;
        right: 0;
        display: flex;
        flex-direction: column;
        row-gap: 3em;
        background: var(--color-black);
        height: 130vh;
        width: 50%;
        padding: 1em;
        z-index: 30;      
    }

    .app-header__mobile-navigation__menu--visible {
        transform: translateX(0);
        transition: transform 300ms ease;
    }

    .app-header__mobile-navigation__menu--hidden { 
        transform: translateX(100%);
        transition: transform 300ms ease;
    }

    .app-header__mobile-navigation__menu__links { 
        display: flex;
        flex-direction: column;
        row-gap: 2em;
        justify-content: center;
        align-items: center;
    }

    .app-header__mobile-navigation__menu__links__link-item {
        list-style-type: none;
    }

    .app-header__mobile-navigation__menu__links__link-item a {
        text-decoration: none;
        text-transform: uppercase;
        font-weight: 700;
        color: var(--color-primary);
    }

    .app-header__mobile-navigation__menu__button { 
        background: transparent;
        border: none;
        outline: none;
        padding: 2em 3em;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        align-self: end;
        position: relative;
    }

    .app-header__mobile-navigation__menu__button__line { 
        position: absolute;
        width: 2em;
        height: 0.2em;
        background-color: var(--color-white);
        transform-origin: center;
    }

    .app-header__mobile-navigation__menu__button__line:nth-child(1) { 
        transform: rotate(40deg);
    }

    .app-header__mobile-navigation__menu__button__line:nth-child(2) { 
        transform: rotate(-40deg);
    }
}

@media screen and (min-width: 576px) { 
    .app-header { 
        /* margin: 0 3rem; */
    }

    .app-header__logo img { 
        width: 50px;
        height: 50px;
    }

    .app-header__mobile-navigation {
        display: none;
    }

    .app-header__main-navigation {
        display: block;
    }

    .app-header__main-navigation__menu { 
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        column-gap: 1em;
    }

    .app-header__main-navigation__menu-item {
        list-style-type: none;
    }

    .app-header__main-navigation__menu-item a{ 
        text-transform: capitalize;
        text-decoration: none;
        font-weight: bold;
        color: var(--color-primary);
    }

    .app-header__main-navigation__menu-item a:hover {
        color: var(--color-violet);
    }
}

@media screen and (min-width: 1023px) {
    .app-header { 
        /* margin: 0 5rem; */
    }

    .app-header__logo img { 
        width: 60px;
        height: 60px;
    }

    .app-header__main-navigation__menu-item a{
        font-size: var(--main-navgiation-menu-item-font);
    }
}
