@media screen and (min-width: 1px) {
    .app-about-section {
        display: grid;
        grid-template-rows: auto auto;
        row-gap: 1.5em;
        padding-top: 7em;
        margin-bottom: 10em;
    }

    .app-about-section__details {
        display: grid;
        grid-template-rows: auto auto;
        row-gap: 1em;
    }
    
    .app-about-section__title {
        color: var(--color-gray);
        font-size: var(--small-title-size);
        font-weight: bold;
    }
    
    .app-about-section__details__description {
        display: grid;
        grid-template-rows: auto auto auto;
        row-gap: 0.8em;
        max-width: 400px;
    }

    .app-about-section__details__description__paragraph {
        color: var(--color-gray);
        line-height: var(--text-line-height);
        font-size: var(--small-text-size);
    }   

    .app-about-section__technologies {
        display: grid;
        grid-template-columns: repeat(2, auto);
        justify-content: space-evenly;
        /* border: 1px solid red; */
    }

    .app-about-section__technologies__gr-1 { 
        display: flex;
        flex-direction: column;
        row-gap: 0.4em;
    }   
    
    .app-about-section__technologies__gr-1__item {
        color: var(--color-gray);
        font-size: 0.9em;
        list-style: center url("../../assets//list-arrow.svg");
    }

    .app-about-section__technologies__gr-2 { 
        display: flex;
        flex-direction: column;
        row-gap: 0.4em;
    }    

    .app-about-section__technologies__gr-2__item { 
        color: var(--color-gray);
        font-size: 0.9em;
        list-style: center url("../../assets//list-arrow.svg");
    }

    .app-about-section__avatar {
        position: relative; 
        justify-self: center;
    }

    .app-about-section__avatar img { 
        height: 200px;
        object-fit: cover;
        object-position: center;
    }

    .app-about-section__avatar__overlay-front { 
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 200px;
        background-color: rgba(31, 24, 63, 0.8);
        transition: background 300ms ease;
    }

    .app-about-section__avatar:hover .app-about-section__avatar__overlay-front {
        background: transparent;
        transition: background 300ms ease;
    }

    .app-about-section__avatar:hover .app-about-section__avatar__overlay-back {
        border-color: var(--color-primary);
        transition: border-color 300ms ease;
    }

}

@media screen and (min-width: 576px) {
    .app-about-section {
        row-gap: 2em;
    }

    .app-about-section__details__description {
        max-width: 400px;
    }

    .app-about-section__details__description__paragraph { 
        font-size: var(--large-text-size);
    }

    .app-about-section__technologies__gr-1__item { 
        font-size: 0.9em;
    }

    .app-about-section__technologies__gr-2__item { 
        font-size: 0.9em;
    }

    .app-about-section__avatar img { 
        height: 250px;
    }

    .app-about-section__avatar__overlay-front { 
        height: 250px;
    }

    .app-about-section__avatar__overlay-back { 
        position: absolute;
        z-index: -1;
        top: 10px;
        left: 10px;
        width: 100%;
        height: 250px;
        border: 4px solid var(--color-violet);
        transition: border-color 300ms ease;
    }
}

@media screen and (min-width: 700px) {
    .app-about-section__details__description { 
        max-width: 700px;
    }
}

@media screen and (min-width: 900px) { 
    .app-about-section { 
        grid-template-rows: 1fr;
        grid-template-columns: 1fr auto;
        row-gap: 0;
        column-gap: 2em;
    }

    .app-about-section__details__description {
        max-width: fit-content;
    }

    .app-about-section__avatar img { 
        width: 300px;
    }
}

@media screen and (min-width: 1024px) { 
    .app-about-section__title { 
        font-size: var(--large-title-size);
    }
}
