@media screen and (min-width: 1px) {
    .app-footer { 
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        row-gap: 1em;
        padding-bottom: 1em;
    }

    .app-footer__title { 
        color: var(--color-gray);
        font-size: 0.9em;
    }

    .app-footer__social-media-icons { 
        display: flex;
        flex-direction: row;
        column-gap: 1em;
    }

    .app-footer__social-media-icons__icon:hover { 
        opacity: 0.5;
    }

    .app-footer__social-media-icons__icon img { 
        width: 15px;
        height: 15px;
    }
}

@media screen and (min-width: 576px) {
    .app-footer__title { 
        font-size: 1em;
    }

    .app-footer__social-media-icons__icon img { 
        width: 18px;
        height: 18px;
        border-radius: 9999px;
    }

    .app-footer__social-media-icons__icon img { 
        width: 18px;
        height: 18px;
        border-radius: 9999px;
    }
}