@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;900&display=swap);
* {
    padding: 0;
    margin: 0;
}

:root { 
    --color-primary: #473BF0;
    --color-violet: #514AB7;
    --color-violet-dark: #161939;
    --color-gray: #ACADBC;
    --color-mobile-menu: #0c0113;
    --color-black: rgb(0, 5, 0);
    --color-white: #FFFFFF;
    --main-navgiation-menu-item-font: 1.3em;
    --small-title-size: 2.5em;
    --small-project-title-size: 1.5em;
    --small-text-size: 1em;
    --large-title-size: 3em;
    --large-project-title-size: 2em;
    --large-text-size: 1.1em;
    --text-line-height: 1.3em;
}

body { 
    font-size: 1em;
    font-family: "Roboto";
    background-color: rgb(0, 5, 0);
    background-color: var(--color-black);
    overflow-x: hidden;
    max-width: 100%;
}

html { 
    scroll-behavior: smooth;
}

#root {
    display: grid;
    grid-template-rows: 1fr;
    justify-content: center;
    position: relative;
}

.item-blured { 
    -webkit-filter: blur(8px); 
            filter: blur(8px);
    transition: -webkit-filter 300ms ease;
    transition: filter 300ms ease;
    transition: filter 300ms ease, -webkit-filter 300ms ease;
}

.item-not-blured { 
    -webkit-filter: blur(0); 
            filter: blur(0);
    transition: -webkit-filter 300ms ease;
    transition: filter 300ms ease;
    transition: filter 300ms ease, -webkit-filter 300ms ease;
}

.highlight { 
    color: #473BF0; 
    color: var(--color-primary);
    font-weight: bold;
}

@media screen and (min-width: 1px) {
    #root { 
        margin: 0 3em;
    }
}

@media screen and (min-width: 1024px) { 
    #root { 
        margin: 0;
    }
}

@media screen and (min-width: 1px) {
    .app-about-section {
        display: grid;
        grid-template-rows: auto auto;
        grid-row-gap: 1.5em;
        row-gap: 1.5em;
        padding-top: 7em;
        margin-bottom: 10em;
    }

    .app-about-section__details {
        display: grid;
        grid-template-rows: auto auto;
        grid-row-gap: 1em;
        row-gap: 1em;
    }
    
    .app-about-section__title {
        color: var(--color-gray);
        font-size: var(--small-title-size);
        font-weight: bold;
    }
    
    .app-about-section__details__description {
        display: grid;
        grid-template-rows: auto auto auto;
        grid-row-gap: 0.8em;
        row-gap: 0.8em;
        max-width: 400px;
    }

    .app-about-section__details__description__paragraph {
        color: var(--color-gray);
        line-height: var(--text-line-height);
        font-size: var(--small-text-size);
    }   

    .app-about-section__technologies {
        display: grid;
        grid-template-columns: repeat(2, auto);
        justify-content: space-evenly;
        /* border: 1px solid red; */
    }

    .app-about-section__technologies__gr-1 { 
        display: flex;
        flex-direction: column;
        grid-row-gap: 0.4em;
        row-gap: 0.4em;
    }   
    
    .app-about-section__technologies__gr-1__item {
        color: var(--color-gray);
        font-size: 0.9em;
        list-style: center url(/static/media/list-arrow.c585d818.svg);
    }

    .app-about-section__technologies__gr-2 { 
        display: flex;
        flex-direction: column;
        grid-row-gap: 0.4em;
        row-gap: 0.4em;
    }    

    .app-about-section__technologies__gr-2__item { 
        color: var(--color-gray);
        font-size: 0.9em;
        list-style: center url(/static/media/list-arrow.c585d818.svg);
    }

    .app-about-section__avatar {
        position: relative; 
        justify-self: center;
    }

    .app-about-section__avatar img { 
        height: 200px;
        object-fit: cover;
        object-position: center;
    }

    .app-about-section__avatar__overlay-front { 
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 200px;
        background-color: rgba(31, 24, 63, 0.8);
        transition: background 300ms ease;
    }

    .app-about-section__avatar:hover .app-about-section__avatar__overlay-front {
        background: transparent;
        transition: background 300ms ease;
    }

    .app-about-section__avatar:hover .app-about-section__avatar__overlay-back {
        border-color: var(--color-primary);
        transition: border-color 300ms ease;
    }

}

@media screen and (min-width: 576px) {
